import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { domain } from '../domain'
import { interceptor } from '../interceptor'

@Component({
  selector: 'app-spdetail',
  templateUrl: './spdetail.component.html',
  styleUrls: ['./spdetail.component.scss']
})
export class SpdetailComponent implements OnInit {

  constructor(private routeinfo : ActivatedRoute) { }

  ngOnInit() {
    this.routeinfo.queryParams.subscribe(queryParams =>{
      this.opName = queryParams.opName;
    })
    this.setShowData();
  }

  private opName : string = "";
  private showData : string;
  private brace : string = "{";

  setShowData(){
      switch(this.opName){
        case 'domain':
        this.showData = domain;break;
        case 'interceptor':
        this.showData = interceptor;break;
      }
  }

}
