import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { webRoute } from './router/web'
import { javaRoue } from './router/java'
import { CarouselComponent } from './bulu/middle.troops/carousel/carousel.component'

const routes: Routes = [...webRoute,...javaRoue,
    {path:'body/carousel',component:CarouselComponent}
  
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
